import { Component, Host, h, Prop } from '@stencil/core';
import { Memoize, parseIfString } from '@springbokagency/stencil-base';
import type { BsImage } from '../../atoms/image/image';
import { TitleColor, TitleSize, TitleTag } from '../../atoms/title/title.types';

/**
 * Test badge card component.
 */
@Component({
  tag: 'bs-test-badge-card',
  styleUrl: 'test-badge-card.scss',
  shadow: true,
})
export class BsTestBadgeCard {
  /** Props */
  /**
   * The test badge title.
   */
  @Prop() cardTitle: string;

  /**
   * The test badge description.
   */
  @Prop() cardText: string;

  /**
   * The test badge image.
   */
  @Prop() cardImage: BsImage | string;

  @Memoize('cardImage')
  get parsedCardImage(): BsImage {
    return parseIfString(this.cardImage);
  }

  render() {
    return (
      <Host>
        <div class="card">
          <div class="image">
            <bs-image src={this.parsedCardImage.src} altText={this.parsedCardImage.altText} />
          </div>
          <div class="content">
            <bs-title
              class="title"
              tag={TitleTag.Span}
              size={TitleSize.Head5}
              text={this.cardTitle}
              color={TitleColor.Black}
            />
            <bs-text class="text" text={this.cardText} />
          </div>
        </div>
      </Host>
    );
  }
}
