:host {
  @include reset-host;
  --image--fit: contain;
}

:host .card {
  display: flex;
  gap: rem(16);
  align-items: center;
  width: 100%;
  height: rem(116);
  padding: rem(10) rem(16) rem(10) rem(8);
  border: 1px solid $gray-800;
  border-radius: rem(8);

  @include mq('min', 'xs') {
    width: rem(285);
  }

  @include mq('min', 'md') {
    width: rem(330);
    height: rem(150);
  }
}

.image {
  --image--height: calc(100px - 20px);
  display: flex;
  align-items: center;
  justify-content: center;
  width: rem(90);
  height: rem(100);
  padding: rem(10);
  background-color: $gray-900;
  border-radius: rem(4);

  @include mq('min', 'md') {
    --image--height: calc(130px - 40px);
    width: rem(120);
    height: rem(130);
    padding: rem(20);
  }
}

.content {
  display: flex;
  flex: 1;
  flex-direction: column;
  gap: rem(2);
  justify-content: flex-start;
  max-height: 100%;
  overflow: hidden;
  text-align: left;
  @include font(body-default);

  @include mq('min', 'md') {
    gap: rem(10);
  }

  .title {
    margin-bottom: rem(5);
    line-height: 1.1;
  }

  .text {
    font-size: rem(12);
    color: $gray-700;

    @include mq('min', 'md') {
      margin-top: 0;
    }
  }
}
